.withIcon:before {
  content: '';
  position: absolute;
  top: 0.375rem;
  left: -1.375rem;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--autocomplete-plugin-svg-container-bg-color); }

.withIcon svg {
  position: absolute;
  top: 0.475rem;
  width: 0.8rem;
  height: 0.8rem;
  left: -1.275rem;
  stroke: var(--autocomplete-plugin-svg-color);
  fill: var(--autocomplete-plugin-svg-color); }
